import uniNavBar from "@/components/uni-nav-bar/uni-nav-bar.vue"
export default {
	components: {uniNavBar},
	data() {
		return {
			searchInputText: ''
		}
	}, 
	onLoad() {
		uni.showLoading({
			title: '加载中...',
			mask: true
		});
		uni.request({
			url: 'https://unidemo.dcloud.net.cn/api/news',
			method: 'GET',
			data: {},
			success: res => {
				console.log(res.data);
				this.news = res.data;
				uni.hideLoading();
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods: {
		checkin(){
			uni.showToast({
			    title: '签到成功+1',
			    duration: 2000,
				icon: "none"
			});
		},
		openlist(e){
			uni.navigateTo({
				url: '../article-list/article-list',
			});
		}
	}
}
